import MXRUtil from '../common/mxrutil.js'


export default {
	components: {
		
	},
	data() {
		return {
			bookDetailM: undefined,
			bookGuid: '9AC6577B5A444E4A9810119C6A4DFBF7',
			bookIconPath: '',
			recommendBooks: [],
			bookTags: [],
			zoneArray: [],
			bookImageContainerStyle: {},
			bookImageStyle: {},
			bgStyle: {},
		}
	},
	onLoad(option) {
		if (option.bookGuid) {
			this.bookGuid = option.bookGuid
		}
	},
	onShow() {
		if (this.bookDetailM == null) {
			MXRUtil.mxr_get('/book/detail/' + this.bookGuid, null, (err, res) => {
				if (err) {
					console.log('>>> err :', err);
				} else {
					this.bookDetailM = res.data.Body
					console.log('res >>> ', res)
				}
			})
		}
	},
	methods: {
		numberToStr: function (number) {
		  if (typeof number === 'number') {
		    if (number / 10000 > 0) {
		      let str = number / 10000.0 + ''
		      if (str.indexOf('.') > -1) {
		        return str.substring(0, Math.min(str.indexOf('.') + 2, str.length)) + 'ä¸‡'
		      }
		    }
		  }
		  return number
		}
	}
}
